const helpers = {
  debounce: (f, ms) => {
    let isCooldown = false;
    return () => {
      if (isCooldown) return;
      f.apply(this, arguments);
      isCooldown = true;
      setTimeout(() => (isCooldown = false), ms);
    };
  },
};

const header = document.querySelector('.site-header');

if (header) {
  if (window.matchMedia('(max-width: 1024px)').matches) {
    const menuWrapper = header.querySelector('.site-header__nav-wrapper');
    const toggleMenuBtn = header.querySelector('.site-header__nav-button');

    const onWindowClick = (evt) => {
      if (!evt.target.matches('.site-header__nav-wrapper, .site-header__nav-wrapper *')) {
        menuWrapper.classList.remove('site-header__nav-wrapper--opened');
        document.removeEventListener('click', onWindowClick);
      }
    };

    toggleMenuBtn.addEventListener('click', (evt) => {
      evt.stopPropagation();
      menuWrapper.classList.toggle('site-header__nav-wrapper--opened');
      document.addEventListener('click', onWindowClick);
    });
  } else {
    document.addEventListener('scroll', () => {
      if (window.scrollY === 0) {
        header.classList.remove('site-header--fixed');
      } else {
        header.classList.add('site-header--fixed');
      }
    });
  }
}
